.vetskitchen-dogclub-banner {
  position: relative;
  padding-top: 60px;
  padding-bottom: 200px;
  font-family: "Signika", sans-serif;
  line-height: 1.7;
  color: #000000;
  text-align: center;
  background: #eee url("../../assets/images/dogclub-banner.webp") no-repeat;
  background-size: cover;
  border-radius: 15px;
}
.vetskitchen-dogclub-banner .acf-innerblocks-container {
  position: relative;
  z-index: 1;
  margin: 0 auto;
  max-width: 460px;
}
.vetskitchen-dogclub-banner h2 {
  font-size: 40px;
  font-weight: 600;
  line-height: 1.1;
  color: #2689de;
}
.vetskitchen-dogclub-banner .wp-block-button {
  margin-top: 50px;
}
.vetskitchen-dogclub-banner__left-image {
  position: absolute;
  bottom: -20px;
  left: 0;
  width: 100%;
  height: auto;
  max-width: 569px;
}
.vetskitchen-dogclub-banner__right-image {
  position: absolute;
  top: 50%;
  right: 20px;
  width: 100%;
  max-width: 230px;
  height: auto;
  transform: translate(0px, -50%);
}
@media screen and (min-width: 1025px) {
  .vetskitchen-dogclub-banner h2 {
    margin-bottom: 23px;
    font-size: 62px;
  }
}
@media screen and (max-width: 1199px) {
  .vetskitchen-dogclub-banner {
    padding-bottom: 70px;
  }
  .vetskitchen-dogclub-banner__left-image {
    top: 22%;
    bottom: 0;
    padding-left: 20px;
    max-width: 319px;
  }
}
@media screen and (max-width: 1024px) {
  .vetskitchen-dogclub-banner__left-image {
    max-width: 239px;
  }
  .vetskitchen-dogclub-banner__right-image {
    top: 39%;
    right: 32px;
    max-width: 140px;
  }
  .vetskitchen-dogclub-banner .wp-block-button {
    margin-top: 40px;
  }
  .vetskitchen-dogclub-banner .acf-innerblocks-container h2 + p ~ p {
    margin: 0 auto;
    max-width: 320px;
    font-size: 15px;
  }
}
@media screen and (max-width: 767px) {
  .vetskitchen-dogclub-banner {
    padding-top: 30px;
    padding-bottom: 40px;
    margin-right: -12px;
    margin-left: -12px;
    background-image: url("../../assets/images/dogclub-banner-mobile.webp");
  }
  .vetskitchen-dogclub-banner .wp-block-image {
    position: relative;
    z-index: 1;
    margin-top: 10px;
    margin-bottom: 0;
  }
  .vetskitchen-dogclub-banner .wp-block-image + .wp-block-button {
    margin-top: -13px;
  }
  .vetskitchen-dogclub-banner .wp-block-image + .wp-block-button > .wp-block-button__link {
    width: 100%;
    max-width: 265px;
  }
}